<template>
  <div class="stocks-grid stocks-grid_1">
    <div>
      <h2 class="stock-name">{{stocki.name}}<span class="stock-price">(price:{{stocki.price | currency}} | Quantity: {{stocki.quantity}})</span></h2>
    </div>
    <div class="form-box">

      <div class="form-content">
        <form>
          <input type="number" placeholder="Quantity" class="stock-form" v-model.number="quantity"   :class="{stockRED: insufficientQuantity}">
          <p class="insuff">{{insufficientQuantity ? 'Insufficient Stock Quantity' : ''}}</p>
        </form>
      </div>
      <button class="form-button" type="submit" @click="sellStock" :disabled="insufficientQuantity || +quantity <= 0 || !Number.isInteger(+quantity)">Sell</button>
    </div>
  </div>
</template>


<script>
import {mapActions} from 'vuex';

export default {
props: ['stocki'],

data(){
  return {
    quantity: 0
  }
},

computed: {
  insufficientQuantity(){
    return this.quantity > this.stocki.quantity;
  }
},

methods: {
  ...mapActions({
  placeSellStock:'sellStock'
}),
  sellStock(){
    const order ={
      stockId: this.stocki.id,
      stockPrice: this.stocki.price,
      quatity: this.quantity
    };
    this.placeSellStock(order);
    this.quantity = 0;
  }
}

}
</script>



<style>

.stocks-grid {
  background-image: linear-gradient(to right, rgba(94, 170, 168, .4), rgba(94, 170, 168, .75));
  /* width:50%; */
  height:25vh;
  display: grid;
  padding: 2rem .5rem 2rem .5rem;
  grid-template-columns: minmax(100%, min-content);
  grid-template-rows: repeat(2, minmax(50%, min-content));
  border-radius:1.5rem;
}

/* .stocks-grid_1 {
  grid-column: 1 / 2;

}
.stocks-grid_2 {
  grid-column: 2 / -1;

}
.stocks-grid_3 {
  grid-column:  1 / 2;
  grid-row:  2 / -1;
}
.stocks-grid_4 {
  grid-column: 2 / -1;
  grid-row:  2 / -1;
} */

.stock-name {
  font-size: 1.5rem;
  display: inline-block;
  font-weight: 300;
  /* background-color:green; */
}

.stock-price {
  font-size: .8rem;
  margin-left: .2rem;
  font-weight: 100;
}

.form-box {
  padding: .4rem;
  /* background-color:red; */
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;

}
.form-content{
  display: flex;
  flex-direction: column;
}

.stock-form {
  border: .1rem solid transparent;
  box-sizing: border-box;
}
.stock-form:focus{
  outline: none;
}
.insuff{
  font-size: 1rem;
  color:red;
  font-weight:300;
}
.stockRED{
  border-style: solid;
  border-color:red;
}

.form-button {
  padding: .3rem 1rem;
  border: none;
  border-style: none;
  font-size: 1.3rem;
  border-radius: 10%;
  background-color: #d8d3cd;
  cursor: pointer;
  display:inline-block;
  transition:all .2s;

}
.form-button:hover{
  background-color: rgba(91,149,148,.9);
  color: white;
}

</style>



