<template>
<nav class="mynav">


  <router-link to="/" class="brand" tag="a">Trader</router-link>
  <div class="links">
    <router-link to="/portfolio" activeClass="active" class="links-text" tag="a">Portfolio</router-link>

    <router-link to="/stocks" activeClass="active" class="links-text" tag="a">Stocks</router-link>
  </div>

  <div>
    <a href="#" class="enday" @click="endDay">EndDay</a>
  </div>

  <div class="dropdown drop-down">
    <a class="btn btn-secondary dropdown-toggle btn-edit" href="#" role="button" id="dropdownMenuLink" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
      Save & Load<span class="caret"></span>
    </a>

    <div class="dropdown-menu" aria-labelledby="dropdownMenuLink">
      <a class="dropdown-item active" href="#" @click="saveData">Save Data</a>
      <a class="dropdown-item" href="#" @click="loadData1">Load Data</a>
    </div>
  </div>

  <div>
    <p class="funds">{{funds | currency}}</p>
  </div>



</nav>



