</template>

<script>
import {mapActions} from 'vuex';

export default {
  computed: {
    funds(){
      return this.$store.getters.funds;
    }
  },

  methods: {
    ...mapActions([
      'randomizeStocks',
      'loadData'
    ]),

    endDay(){
      this.randomizeStocks();
    },

    saveData(){
      const data={
        funds: this.$store.getters.funds,
        stockPortfolio: this.$store.getters.stockPortfolio,
        stocks: this.$store.getters.stocks
      };
      this.$http.put('data.json',data);
    },

    loadData1(){
      this.loadData();
    }
  }
}
</script>

<style>
.mynav {
  padding: 1rem;
  /* background-color:yellow; */
  width: 100%;
  display: flex;
  align-items: center;
  margin-bottom: 3rem;
  font-size: 1.3rem;
}



.brand:link,
.brand:visited {
  text-decoration: none;
  /* background-color:green; */
  color: #5eaaa8;
  font-size: 1.5rem;
  font-weight:600;
  font-family: 'Megrim', cursive;
}

.links {
  list-style: none;
  display: flex;
  /* justify-content: flex-start; */
  align-self: center;
  /* background-color:red; */
  width: inherit;
  height: 100%;

  /* margin-top:1rem; */

}

.links-text:link,
.links-text:visited {
  text-decoration: none;
  margin: 0 0rem 0 2rem;
  color: #d8d3cd;
  font-weight: 200;
  transition: all .2s;
}
.links-text:hover{
  color: #5eaaa8;
}

.enday:link,
.enday:visited{
  text-decoration:none;
  font-weight: 200;
  display:inline-block;
  margin-right:2rem;
  color: #d8d3cd;
}


.btn-edit{
  background-color:#5eaaa8;
  background-color:transparent;
  border-radius:none;
  border-radius:0;
  border-style:none;
}

.funds{
  font-size:1rem;
  color:#7BBE40;
  font-weight:300;
  padding:.5rem 1rem;
  background-color:transparent;
  margin-top:1rem;
  box-shadow:0rem 0rem 5rem #5eaaa8;
}
.drop-down{
  margin-right:1rem;
}
</style>



