import Vue from 'vue'
import VueRouter from 'vue-router';
import App from './App.vue';
import VueResource from 'vue-resource';
import {myRoutes} from './routes';
import store from './store/store';
Vue.use(VueRouter);

/***CREATING the ROUTER***/
const router = new VueRouter({
  mode:'history',
  routes: myRoutes
});

/***CREATING the VUE_RESOURCE***/
Vue.use(VueResource);

Vue.http.options.root = 'https://stockapp-3de79.firebaseio.com/';


/*** FILTERS ***/
Vue.filter('currency',value => {
  return '' + value.toLocaleString();
});





/*** APP ***/
new Vue({
  el: '#app',
  router,
  store,
  render: h => h(App)
});



