<template>
  <div class="home">
    <h1 class="h1">Trade or View your Portfolio</h1>
    <h6>You may Save & load your Data</h6>
    <h6>Click on 'End Day' to begin a New Day!</h6>
    <p>Your Funds: {{funds | currency}}</p>
  </div>

</template>

<script>
export default {
  computed: {
    funds(){
      return this.$store.getters.funds;
    }
  }
}
</script>



<style>
.home{
  background-image:linear-gradient(to right bottom,rgba(67,157,157,.5),black);
  padding:3rem;
  text-align:left;
  color: #d8d3cd;
  font-weight:200;
}

.h1{
  margin-bottom:2rem;
}
p{
  font-size:1.5rem;
  color: #ffff;
  font-weight:300;
  margin-top:1.5rem;
}
</style>



