<template>
<section class="stocks-container">
<app-stock v-for="stockiee in stocks" :stocki ="stockiee"></app-stock>
<!-- <h2 :class="{empty:show}">No Stocks traded Yet!</h2> -->

</section>
</template>


<script>
import Stock from './Stock.vue';
import {mapGetters} from 'vuex';

export default {

computed: {
  ...mapGetters({
    stocks:'stockPortfolio'
  }),
  show(){
    return this.$store.getters.show();
  }
},

components: {
  appStock : Stock
}
}
</script>




<style>
.stocks-container {

  background-image: radial-gradient(rgba(0, 0, 0, .65), rgba(0, 0, 0, .65));
  background-image: transparent;
  padding: 3rem 2rem 3rem 2rem;
  width: 100%;

  display: grid;
  grid-gap: 2rem;
  grid-template-columns: minmax(min-content, 50%) minmax(min-content, 50%);
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
}

.empty{
  visibility:hidden;
}

/* .form-button:hover {
  background-color: rgba(94, 170, 168, .2);
  border: 0.1rem solid;
  color: white;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
} */

/* .form-btn:active,
.form-btn:focus {
  outline: none;
  box-shadow: 0 .5rem 1rem rgba(black, .2);
} */
</style>



