<template>
  <div class="stocks-grid stocks-grid_1">
    <div>
      <h2 class="stock-name">{{stock.name}}<span class="stock-price">(price:{{stock.price | currency}})</span></h2>
    </div>
    <div class="form-box">
      <div class="form-content">
        <form>
          <input type="number" placeholder="Quantity" class="stock-form" v-model.number="quantity"
          :class="{stockRED: insufficientFunds}">
          <p class="insuff">{{insufficientFunds ? 'Insufficient Funds' : ''}}</p>
        </form>
      </div>

      <button class="form-button" type="submit" @click="buyStock" :disabled="insufficientFunds || +quantity <= 0 || !Number.isInteger(+quantity)">Buy</button>
    </div>
  </div>
</template>


<script>

export default {
props: ['stock'],

data(){
  return {
    quantity: 0
  }
},

computed: {
  funds(){
    return this.$store.getters.funds;
  },
  insufficientFunds(){
    return this.quantity * this.stock.price > this.funds;
  }
},

methods: {
  buyStock(){
    const order ={
      stockId: this.stock.id,
      stockPrice: this.stock.price,
      quantity: this.quantity
    };
    // console.log(order);
    this.$store.dispatch('buyStock',order);
    this.quantity = 0;

  }
}

}
</script>



<style scoped>

.stocks-grid {
  background-image: linear-gradient(to right, rgba(94, 170, 168, .4), rgba(94, 170, 168, .75));
  /* width:50%; */
  height:25vh;
  display: grid;
  padding: 2rem;
  grid-template-columns: minmax(100%, min-content);
  grid-template-rows: repeat(2, minmax(50%, min-content));
  border-radius:1.5rem;
}

/* .stocks-grid_1 {
  grid-column: 1 / 2;

}
.stocks-grid_2 {
  grid-column: 2 / -1;

}
.stocks-grid_3 {
  grid-column:  1 / 2;
  grid-row:  2 / -1;
}
.stocks-grid_4 {
  grid-column: 2 / -1;
  grid-row:  2 / -1;
} */

.stock-name {
  font-size: 1.5rem;
  display: inline-block;
  font-weight: 300;
  /* background-color:green; */
}

.stock-price {
  font-size: .9rem;
  margin-left: .2rem;
  font-weight: 200;
}

.form-box {
  padding: .4rem;
  /* background-color:red; */
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;

}
.form-content{
  display: flex;
  flex-direction: column;
}

.stock-form{
  border: .1rem solid transparent;
  box-sizing: border-box;
  border-top-style: hidden;
  border-right-style: hidden;
  border-left-style: hidden;
  border-bottom-style: groove;
}
.stock-form:focus{
  outline: none;
}
.insuff{
  font-size: 1rem;
  color:red;
  font-weight:300;
}

.stockRED{
  border-style: solid;
  border-color:red;
}

.form-button {
  padding: .3rem 1rem;
  border: none;
  border-style: none;
  font-size: 1.2rem;
  font-weight:200;
  border-radius: 10%;
  background-color: #2E5946;
  color: white;
  cursor: pointer;
  display:inline-block;
  transition:all .2s;

}
.form-button:hover{
  background-color: #D6E0E0;
  color: black;
}

</style>



