<template>
<div class="container">
  <app-header></app-header>
  <div class="row">
    <div class="col-md-12">
      <transition name="slide" mode="out-in">
        <router-view></router-view>
      </transition>

    </div>
  </div>

</div>
</template>



<script>
import Header from './components/Header.vue';

export default {

  components: {
    appHeader: Header
  },

  created(){
    this.$store.dispatch('initStocks');
  }
}
</script>



<style>
  body {
    height:100vh;
    padding: 2rem;
    background-image: radial-gradient(rgba(0,0,0,.35),rgba(0,0,0,.95)),url("./img/img4.jpg");
    background-position: center;
    background-size: cover;
    color:white;
  }

  .slide-enter-active{
    animation: slide-in 200ms ease-out forwards;
  }
  .slide-leave-active{
    animation: slide-out 200ms ease-out forwards;
  }

  @keyframes slide-in{
    from {
      transform: translateY(-30px);
      opacity:0;
    }
    to {
      transform:  translateY(0);
      opacity:1;
    }
  }
  @keyframes slide-out{
    from {
      transform: translateY(0);
      opacity:1;
    }
    to {
      transform:  translateY(-30px);
      opacity:0;
    }
  }
</style>



